//查询搜索
handleSubmit = e => {
  e.preventDefault();
  this.props.form.validateFields((err, values) => {
    if (!err) {
      console.log(err);
      console.log('输入的结果为', values);
      var num=localStorage.getItem('dai_type');
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
      console.log(num);
      if(loginid!='' && user!=""){
       
      axios.post('/index.php/dai/tong',{ 
        name:user,
        num:loginid,
        value:values,
        type:num
         })
         .then((res)=>{
           if(res.data.code==200){
            console.log(res);
            var ke=res.data.data;
            var kenum=[];
            var kedate=[];
            if(num=='1'){
              ke.map((m,n)=>{
                kenum.push(`${m.date}号`);
                kedate.push(m.total)
              })
            }else if(num=='2'){
              ke.map((m,n)=>{
                kenum.push(`${m.date}周`);
                kedate.push(m.total)
              })
            }else if(num=='3'){
              ke.map((m,n)=>{
                kenum.push(`${m.date}月`);
                kedate.push(m.total)
              })
            }else if(num=='4'){
              ke.map((m,n)=>{
                kenum.push(`${m.date}季度`);
                kedate.push(m.total)
              })
            }else if(num=="5"){
              ke.map((m,n)=>{
                kenum.push(`${m.date}年度`);
                kedate.push(m.total)
              })
            }
             console.log(kenum,kedate);
            var  fang=res.data.list;
            var fangnum=[];
            var fangdate=[];
            fang.map((j,k)=>{
              fangnum.push(j.s)
              fangdate.push(`${j.date}号`)
            })
            // console.log(fangnum,fangdate);
              this.setState({
                   ke:kedate,      //带看客次数量
                   fang:fangnum,    //带看房次数量
                   date:kenum,      //横轴时间
                   ids:res.data.ids
              })
                this.getbar();
           
           }else {
           
           }
         }).catch(error=>{
           console.log(error)
         })
        }

    }
  })
}


handleSubmitForm2 = e => {
  e.preventDefault();
  this.props.form.validateFields((err, values) => {
    if (!err) {
      console.log(err);
      console.log('输入的结果为新老客户带看量', values);
       var num=  localStorage.getItem('kelistid');
       console.log(num);
        if(loginid!='' && user!=""){
        axios.post('/index.php/dai/utong',{
           name:user,
           num:loginid,
           type:num,
           value:values
        })
        .then((res)=>{
          console.log(res.data.n);
          var number1=JSON.parse(res.data.n[0]);
          var number2=JSON.parse(res.data.n[1]);
          console.log(number1,number2);
          var num1=[];
          num1.push(number1,number2);
          console.log(num1);
          var  zongdata=0;
          num1.map((m,n)=>{
                // console.log(m.count);
                zongdata+=m.count;

                console.log(zongdata);
          })
          this.setState({
            data:num1,
            zong:zongdata
          })
          const dv= new DataView();
          dv.source(this.state.data).transform({
            type: "percent",
            field: "count",
            dimension: "item",
            as: "percent"
          });
          const cols = {
            percent: {
              formatter: val => {
                val = val * 100 + "%";
                return val;
              }
            }
          };
          this.setState({
             dvv:dv,
             colss:cols
          })
        }).catch((error)=>{
          console.log(error)
        })
      }


    }
  })

}







