import React ,{Component} from 'react';
import { Select,Table } from 'antd';
import axios from 'axios'
class dengjiA extends Component{
      constructor(props){
        super(props)
        this.state={
            datanum:[],
        }
      }
     
       componentDidMount(){
              this.genDetail();
       }
       genDetail(){
        const  loginid=localStorage.getItem('loginid');
        const user=localStorage.getItem('name');
        var id=this.props.location.data;
        console.log(id);
        if(loginid!='' && user!=""){
            const  loginid=localStorage.getItem('loginid');
            const user=localStorage.getItem('name');
          axios.post('/index.php/user/tonglist',{
            num:loginid,
            name:user,
            ids:id
          })
          .then((res)=>{
            // console.log(res);
            if(parseInt(res.status) === 200){
                console.log(res);
               var number=res.data.data;
               var arr=[];
               if(number.length>0){
               number.map((v,j)=>{
               arr.push({
                       key:v.id,
                       tag:v.grade,
                       label:v.label,
                       re:v.re,
                       dai:v.dai,
                       fu:v.fu,
  
                       customername:v.name,
                       jkproject:v.project,
                       jkorigin:v.port,
                       rtime: v.time,
                       zjtime:v.g_time,
                       isnow:v.isnow,
  
                       txdate:v.grade,
                       rpeople:v.s_id,
                       wtpeople:v.sid,
                    //    tel:v.tel,
                       type:v.peo
                   })
                   this.setState({
                    datanum:arr,
                    loading:false,
                    total:res.data.total,
                   })
               })
              }else{
                this.setState({
                  datanum:'',
                  loading:false
                 })
              }
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })
       
        }
     
          }
     
      render(){
        const columns = [
            {
              title: '',
              dataIndex: 'tag',
                render:(text,record) =>{
                var lei=record.tag
                //  var type=record.label;
                //  var re=record.re;
                //  var dai=record.dai;
                  // console.log(record);
                 var str=""
                 if( lei =="A" || lei=="A类" ){
                    return(
                       <a className="iconfont icon-zhongdianbiaozhu " href="javascript:;" style={{color:'red'}}></a> 
                    )
                 }
              }
  
            },
            {
              title: '',
              dataIndex: 'tag1',
              render:(text,record) =>{
                var type=record.label;
                if(type=="一客多看"){
                  return(
                    <a className="iconfont icon-ai-connection" href="javascript:;" style={{color:'rgba(101,195,255,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '标签',
              dataIndex: 'tag2',
              render:(text,record) =>{
                var num=record.fu;
                if(num=="复看"){
                  return(
                    <a className="iconfont icon-renshu" href="javascript:;" style={{color:'rgba(196,132,255,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '',
              dataIndex: 'tag3',
                render:(text,record) =>{
                var dai=record.dai;
                if(dai=="新客" || dai=="老客" ){
                  return(
                    <a className="iconfont icon-wo" href="javascript:;" style={{color:'rgba(41,204,114,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '',
              dataIndex: 'tag4',
              render:(text,record) =>{
                var re=record.re;
              if(re=="成交"){
                return(
                  <a className="iconfont icon-chengjiao" href="javascript:;" style={{color:'rgba(255,191,52,1)'}}></a> 
                )
              }
            }
            },
            {
              title: '客户姓名',
              dataIndex: 'customername',
            },
            {
              title: '进客项目',
              dataIndex: 'jkproject',
            },
            {
              title: '进客来源',
              dataIndex: 'jkorigin',
            },
            {
              title: `登记时间`,
              dataIndex: 'rtime',
            //   sorter:true,
            },
            {
              title: '最近跟进时间',
              dataIndex: 'zjtime',
              render:(text,record)=>{
                   var now=record.isnow;
                   var time=record.zjtime
                   if(now==0 || now==null){
                      return(
                          <span >{time}</span>
                      )
                   }if(now==1){
                       return(
                        <span style={{color:'red'}}>{time}</span>
                       )
                   }
              },
            // sorter:true,
            },
            {
              title: '客户等级',
              dataIndex: 'txdate',
            },
            {
              title: '登记人',
              dataIndex: 'rpeople',
            },
            {
              title: '委托人',
              dataIndex: 'wtpeople',
            },
            {
              title: '类型',
              dataIndex: 'type',
            },
          
  
          ];
              const { datanum }=this.state;
        return(
          <div className="dengjia">
                 <Table  columns={columns} dataSource={datanum} size="middle" className="genjintablle" />
          </div>
          ) 
      }
}
  export default dengjiA