import React ,{Component} from 'react';
import {Table} from 'antd'
import axios from 'axios'
class chengjiao extends Component{
      constructor(props){
        super(props)
        this.state={
            datanum:[] 
        }
      }



      componentDidMount(){
             this.getlist();
      }
      getlist(){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        var id=this.props.location.data;
        console.log(id);
        //项目列表
        if(loginid!='' && user!=""){
          axios.post('/index.php/recording/tongrelist',{
            name:user,
            num:loginid,
            ids:id
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
               console.log(res);
               var number=res.data.data;
               if(number.length>0){
               number.map((v,j)=>{
               this.state.datanum.push({
                       key:v.id,
                      //  tag:`${v.grade}/${v.label}`,
                       tag:v.grade,
                       label:v.label,
                       re:v.re,
                       dai:v.dai,
                       fu:v.fu,
  
  
  
                       customername:v.name,
                       dkproject:v.project,
                       jkorigin:v.port,
                       dktime:v.r_time,
                       dkpeople:v.s_id,
                   })
               var  datanum=this.state.datanum
                   this.setState({
                    datanum:datanum,
                    loading:false
                   })
               })
              }else{
                this.setState({
                  datanum:'',
                  loading:false
                 })
              }
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })
       //区域数据
        }   
      }
      render(){
        const columns = [
            {
              title: '',
              dataIndex: 'tag',
              width:30,
                render:(text,record) =>{
                var lei=record.tag
                //  var type=record.label;
                //  var re=record.re;
                //  var dai=record.dai;
                  // console.log(record);
                 var str=""
                 if( lei =="A" || lei=="A类" ){
                    return(
                       <a className="iconfont icon-zhongdianbiaozhu " href="javascript:;" style={{color:'red'}}></a> 
                    )
                 }
              }
  
            },
            {
              title: '标',
              dataIndex: 'tag1',
              width:30,
              render:(text,record) =>{
                var type=record.label;
                if(type=="一客多看"){
                  return(
                    <a className="iconfont icon-ai-connection" href="javascript:;" style={{color:'rgba(101,195,255,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '',
              dataIndex: 'tag2',
              width:30,
              render:(text,record) =>{
                var num=record.fu;
                if(num=="复看"){
                  return(
                    <a className="iconfont icon-renshu" href="javascript:;" style={{color:'rgba(196,132,255,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '签',
              dataIndex: 'tag3',
              width:30,
                render:(text,record) =>{
                var dai=record.dai;
                if(dai=="新客" || dai=="老客" ){
                  return(
                    <a className="iconfont icon-wo" href="javascript:;" style={{color:'rgba(41,204,114,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '',
              dataIndex: 'tag4',
              width:30,
              render:(text,record) =>{
                var re=record.re;
              if(re=="成交"){
                return(
                  <a className="iconfont icon-chengjiao" href="javascript:;" style={{color:'rgba(255,191,52,1)'}}></a> 
                )
              }
            }
            },
            {
              title: '客户姓名',
              dataIndex: 'customername',
            },
            {
              title: '成交项目',
              dataIndex: 'dkproject',
            },
            {
              title: '进客来源',
              dataIndex: 'jkorigin',
            },
            {
              title: '成交日期',
              dataIndex: 'dktime',
            },
            {
              title: '带看人',
              dataIndex: 'dkpeople',
            },
          ];
        return(
          <div className="chengjiao">
                        <Table  columns={columns} dataSource={this.state.datanum}  />
          </div>
          )
      }
}
  export default chengjiao