import React ,{Component} from 'react';
import { Select,Table } from 'antd';
import axios from 'axios'

class GenJinLiang extends Component{
      constructor(props){
        super(props)
        this.state={
            gennum:[],
            loading:true,
        }
      }
     
       componentDidMount(){
              this.genDetail();
       }
       genDetail(){
        const  loginid=localStorage.getItem('loginid');
        const user=localStorage.getItem('name');
        var id=this.props.location.data;
        console.log(id);
        if(loginid!='' && user!=""){
             axios.post('/index.php/gen/tonggenlist',{
               ids:id,
               num:loginid, 
               name:user
             })
             .then((res)=>{
               //  console.log(res);
               if(parseInt(res.data.code) === 200){
                 //  console.log(res);
                  var gen=res.data.data
                  if(gen.length>0){
     
                 
                      console.log(gen);
                      var gendata=[]
                      gen.map((v,j)=>{
                   gendata.push({
                   key:v.id,
                   gjtime: v.time,
                   gjcontent: v.content,
                   yperson:v.s_id,
                   txtime:v.t_time,
                   rtype:v.type,
                      })
                      this.setState({
                       gennum:gendata,
                       loading:false,
                      })
                  })
                 }
                 else{
                   this.setState({
                     gennum:'',
                     loading:false,
                    })
                 }
               }else{
                  console.log("请求数据失败")
               }
             }).catch((error)=>{
               console.log(error)
             })
           }
     
          }
     
      render(){
        const columns1 = [
            {
              title: '跟进时间',
              dataIndex: 'gjtime',
             
            },
            {
              title: '跟进内容',
              dataIndex: 'gjcontent',
            },
            {
              title: '业务员',
              dataIndex: 'yperson',
            },
            {
              title: '提醒日期',
              dataIndex: 'txtime',
            },
           
          ];
        return(
          <div className="genjinliang">
                 <Table  columns={columns1} dataSource={this.state.gennum} size="middle" className="genjintablle" loading={this.state.loading}/>
          </div>
          )
      }
}
  export default GenJinLiang