import React ,{Component} from 'react';
import{Breadcrumb,Tabs,DatePicker,Form,Select,Cascader,Input, LocaleProvider,Button} from 'antd'
import {
  G2,
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Legend,
  View,
  Guide,
  Shape,
  Facet,
  Util
} from "bizcharts";
import zhCN from 'antd/lib/locale-provider/zh_CN';
import DataSet from "@antv/data-set";
import axios from 'axios'
import  '../../../style/statistics/volume.scss'
import { Link } from 'react-router-dom'
import $ from 'jquery'
const {Option}= Select;
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChangeDate(date, dateString) {
  console.log(date, dateString);
}
function onChange(value) {
  console.log(value);
}
const { DataView } = DataSet;
const { Html } = Guide;
class kehuForm extends Component{
      constructor(props){
        super(props)
        this.state={
          data:[],
          dvv:[],
          colss:[],

          number:[],
          date:[],

          cheng:[],
          qu:[],
          zu:[],
          peo:[],
        }
      }
      componentDidMount(){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        var num=1;
        if(loginid!='' && user!=""){
          axios.post('/index.php/user/tong',{
            name:user,
            num:loginid,
            type:num
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
              console.log(res.data.data);
              var num=res.data.data;
              var arr=[];
              num.map((j,k)=>{
                     arr.push({
                          year: `${j.date}号`,
                          sales:j.total
                     })
              })
              console.log(arr);
               this.setState({
                  number:arr
               })
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })

        }
        this.getCity();
      }
      selectDate=(e)=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        $(e.target).addClass("current").siblings().removeClass("current")
        var num=e.target.getAttribute('data-key');
        localStorage.setItem('keliang',num);
         console.log(num);
      

    }
//区域数据
getCity(){
  if(loginid!='' && user!=""){
    var loginid=localStorage.getItem('loginid');
    var user=localStorage.getItem('name');
  axios.post('/index.php/qu/one',{ 
    name:user,
    num:loginid
     })
     .then((res)=>{
       if(res.data.code==200){
        console.log(res.data.data);
        var city=res.data.data;
        if(city){
            this.setState({
              cheng:city
            })
        }
       
       }else {
       
       }
     }).catch(error=>{
       console.log(error)
     })
    }
}
 handleSelectCity(value){
  if(loginid!='' && user!=""){
    var loginid=localStorage.getItem('loginid');
    var user=localStorage.getItem('name');
  axios.post('/index.php/qu/two',{ 
    name:user,
    num:loginid,
    id:value
     })
     .then((res)=>{
       if(res.data.code==200){
        console.log(res.data.data);
        var num=res.data.data;
        if(num){
            this.setState({
              qu:num
            })
        }
       
       }else {
       
       }
     }).catch(error=>{
       console.log(error)
     })
    }

}
handleSelectQu(value){
  console.log(value);
  if(loginid!='' && user!=""){
 var loginid=localStorage.getItem('loginid');
 var user=localStorage.getItem('name');
axios.post('/index.php/qu/two',{ 
 name:user,
 num:loginid,
 id:value
  })
  .then((res)=>{
    if(res.data.code==200){
     console.log(res.data.data);
     var data=res.data.data;
     if(data){
         this.setState({
           zu:data
         })
     }
    
    }else {
    
    }
  }).catch(error=>{
    console.log(error)
  })
 }
}
changeZu=(value)=>{
console.log(value);
if(loginid!='' && user!=""){
 var loginid=localStorage.getItem('loginid');
 var user=localStorage.getItem('name');
axios.post('/index.php/qu/peo',{ 
 name:user,
 num:loginid,
 id:value
  })
  .then((res)=>{
    if(res.data.code==200){
     console.log(res.data.data);
     var number=res.data.data;
     if(number){
         this.setState({
           peo:number
         })
     }
    
    }else {
    
    }
  }).catch(error=>{
    console.log(error)
  })
 }
}

//查询搜索
handleSubmit = e => {
  e.preventDefault();
  this.props.form.validateFields((err, values) => {
    if (!err) {
      console.log(err);
      console.log('输入的结果为', values);
      var numid=localStorage.getItem('keliang');
      console.log(numid);
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
        //  if(num==1){
          if(loginid!='' && user!=""){
            axios.post('/index.php/user/tong',{
              name:user,
              num:loginid,
              type:numid,
              value:values
            })
            .then((res)=>{
              if(parseInt(res.status) === 200){
                console.log(res.data.data);
                var num=res.data.data;
                var arr=[];
                if(numid=='1'){
                    num.map((j,k)=>{
                      arr.push({
                          year: `${j.date}号`,
                          sales:j.total
                      })
                    })
                }else if(numid=='2'){
                    num.map((j,k)=>{
                      arr.push({
                          year: `${j.date}周`,
                          sales:j.total
                      })
                    })
                }else  if(numid=='3'){
                  num.map((j,k)=>{
                    arr.push({
                        year: `${j.date}月`,
                        sales:j.total
                    })
                  })
                } else  if(numid=='4'){
                    num.map((j,k)=>{
                      arr.push({
                          year: `${j.date}季度`,
                          sales:j.total
                      })
                    })
                }else if( numid=='5'){
                    num.map((j,k)=>{
                      arr.push({
                          year: `${j.date}年度`,
                          sales:j.total
                      })
                    })
                }
              
                console.log(arr);
                 this.setState({
                    number:arr
                 })
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
  
          }

    }
  })


}




      render(){
        const { getFieldDecorator } = this.props.form;
        const { dainum,cheng,qu,zu,peo,total,zong} =this.state;
        const options = [
          {
            value: '浙江',
            label: '浙江',
            children: [
              {
                value: '杭州',
                label: '杭州',
              },
              {
                value: '宁波',
                label: '宁波',
              },
            ],
          },
          {
            value: '贵州',
            label: '贵州',
            children: [
              {
                value: '贵阳',
                label: '贵阳',
              },
              {
                value: '贵阳2',
                label: '贵阳2',
              },
            ],
          },
        ];
        const cols = {
          sales: {
            tickInterval: 20
          }
        };
        
        const  { number } =this.state;
        return(
          <LocaleProvider locale={zhCN}>
          <div className="volume">
          <Breadcrumb>
              <Breadcrumb.Item ><Link to="/statistics/kehuliang"> 统计分析</Link></Breadcrumb.Item>
              <Breadcrumb.Item ><Link to="/statistics/kehuliang">客户量 </Link></Breadcrumb.Item>
          </Breadcrumb>
              <div className="option">
                <Form layout="inline"  onSubmit={this.handleSubmit}>
                  <Form.Item label='信息来源'>
                  {getFieldDecorator('port', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select
                 style={{width:310,marginRight:"9px"}}
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                  allowClear
                >
                   <Option value="线上推广1">线上推广1</Option>
                  <Option value="线上推广2">线上推广2</Option>
                  <Option value="线上电话1">线上电话1</Option>
                  <Option value="线上电话2">线上电话2</Option>
                  <Option value="自主">自主</Option>
                  <Option value="咨询">咨询</Option>
                  <Option value="新媒体">新媒体</Option>
                  <Option value="百度文库">百度文库</Option>
                  <Option value="留言">留言</Option>
                </Select>,
              )}
              </Form.Item>
                  <Form.Item label="城市" layout="inline">
                      {getFieldDecorator('city', {
                          rules: [{ required: false, message: '请选择省' }],
                        })(
                              <Select  style={{ width:310}} onChange={this.handleSelectCity.bind(this)}  allowClear>
                              {
                                cheng.length>0?cheng.map((m,n)=>{
                                      return(
                                        <Option value={m.id} key={m.id}>{m.name}</Option>
                                      )
                                }) : ''
                            }
                          </Select>
                        )}
                </Form.Item>   
                  <Form.Item label="区域" layout="inline">
                      {getFieldDecorator('area', {
                          rules: [{ required: false, message: '请选择省' }],
                        })(
                          <Select style={{ width:300,marginRight:20}} onSelect={this.handleSelectQu.bind(this)}  allowClear>
                          {
                            qu.length>0?qu.map((m,n)=>{
                                  return(
                                  <Option value={m.id} key={n}>{m.name}</Option>
                                  )
                            }) :''
                      } 
                        </Select>
                        )}
                  </Form.Item>
                  <Form.Item label="组别" layout="inline">
                        {getFieldDecorator('department', {
                            rules: [{ required: false, message: '请选择省' }],
                          })(
                            <Select  style={{ width:300,}} onChange={this.changeZu.bind(this)}  allowClear>
                            {
                                zu.length>0 ? zu.map((m,n)=>{
                                      return(
                                        <Option value={m.id } key={n}>{m.name} </Option>
                                      )
                                }) :''
                            }
                        
              
                          </Select>
                          )}
                    </Form.Item>
                  <Form.Item label="人 员" >
                        {getFieldDecorator('id', {
                            rules: [{ required: false, message: '请选择省' }],
                          })(
                            <Select
                              placeholder="所有"
                              onChange={this.handleSelectChange}
                              style={{width:310,marginLeft:25}}
                              allowClear
                            >
                              {
                                  peo.length>0 ? peo.map((m,n)=>{
                                    return(
                                      <Option value={m.id } key={n}>{m.name} </Option>
                                    )
                              }) :''
                              }
                            </Select> 
                          )}
                   </Form.Item>
            


            <Form.Item>
            <ul className="time">
                    <li onClick={this.selectDate.bind(this)} data-key={'1'} >日</li>
                    <li onClick={this.selectDate.bind(this)} data-key={'2'}>周</li>
                    <li onClick={this.selectDate.bind(this)}  data-key={'3'}>月</li>
                    <li onClick={this.selectDate.bind(this)} data-key={'4'}>季度</li>
                    <li onClick={this.selectDate.bind(this)}  data-key={'5'}>年度</li>
            </ul>
            </Form.Item>
            <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
                        查询
            </Button>



            <Form.Item>
          {/* <RangePicker onChange={onChangeDate} style={{width:400}} /> */}
          </Form.Item>
            </Form>
                   
        </div>
        <Chart height={500} data={number} scale={cols} width={1000}  padding={30} style={{ float:'left'}}>
        <Legend position="bottom" offsetX={-20} offsetY={-80}/>
          <Axis name="year" />
          <Axis name="sales" />
          <Tooltip
            crosshairs={{
              type: "y"
            }}
          />
          <Geom type="interval" position="year*sales" >
           <Label position="top" content={["sales*year", (sales, year)=>{
              return `${sales}`;
            }]}
            textStyle={{
              fontSize: '18',
            }}
          />
          </Geom>
          
        </Chart>
           <Link to="/statistics/kedetail" style={{ float:'left',color:'rgba(24,144,255,1)',fontSize:'18px'}}>查看详情</Link>
          </div>
          </LocaleProvider>
          )
      }
}
const kehuLiang=Form.create()(kehuForm);
  export default kehuLiang