          <Chart height={400} data={number} scale={cols} width={1400}  padding={30}>
          <Axis name="year" />
          <Axis name="sales" />
          <Tooltip
            crosshairs={{
              type: "y"
            }}
          />
          <Geom type="interval" position="year*sales" >
            <Label position="top" content={["sales*year", (sales, year)=>{
                  return `${sales}`;
                }]}
                textStyle={{
                  fontSize: '18',
                }}
              />
          </Geom>
        </Chart>
