import React ,{Component} from 'react';
import  axios from 'axios'
import { 
   Table,
    } 
from 'antd';
class daikanzong extends Component{
      constructor(props){
        super(props)
        this.state={
            datanum:[]
        }
      }


      componentDidMount(){
       this.getProjectData();

      }
      getProjectData(){
        var id=this.props.location.data;
        console.log(id);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/dai/tongdailist',{
          name:user,
          num:loginid,
          ids:id
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
            console.log(res);
             var number=res.data.data;
             if(number.length>0){
             var arr=[];
             number.map((v,j)=>{
           arr.push({
                     key:v.id,
                     tag:v.grade,
                     label:v.label,
                     re:v.re,
                     dai:v.dai,
                     fu:v.fu,
                     customername:v.name,
                     dkproject:v.project,
                     jkorigin:v.port,
                     dktime:v.d_time,
                     dkpeople:v.s_id,
                 })
                 this.setState({
                  datanum:arr,
                  loading:false,
                  total:res.data.total
                 })
             })
               
            }else{
              this.setState({
                datanum:'',
                loading:false
               })
            }
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
      }


      render(){
        const columns = [
            {
              title: '',
              dataIndex: 'tag',
                render:(text,record) =>{
                var lei=record.tag
                 var str=""
                 if( lei =="A" || lei=="A类" ){
                    return(
                       <a className="iconfont icon-zhongdianbiaozhu " href="javascript:;" style={{color:'red'}}></a> 
                    )
                 }
              }
  
            },
            {
              title: '',
              dataIndex: 'tag1',
              render:(text,record) =>{
                var type=record.label;
                if(type=="一客多看"){
                  return(
                    <a className="iconfont icon-ai-connection" href="javascript:;" style={{color:'rgba(101,195,255,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '标签',
              dataIndex: 'tag2',
              render:(text,record) =>{
                var num=record.fu;
                if(num=="复看"){
                  return(
                    <a className="iconfont icon-renshu" href="javascript:;" style={{color:'rgba(196,132,255,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '',
              dataIndex: 'tag3',
                render:(text,record) =>{
                var dai=record.dai;
                if(dai=="新客" || dai=="老客" ){
                  return(
                    <a className="iconfont icon-wo" href="javascript:;" style={{color:'rgba(41,204,114,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '',
              dataIndex: 'tag4',
              render:(text,record) =>{
                var re=record.re;
              if(re=="成交"){
                return(
                  <a className="iconfont icon-chengjiao" href="javascript:;" style={{color:'rgba(255,191,52,1)'}}></a> 
                )
              }
            }
            },
            {
              title: '客户姓名',
              dataIndex: 'customername',
            },
            {
              title: '带看项目',
              dataIndex: 'dkproject',
            },
            {
              title: '进客来源',
              dataIndex: 'jkorigin',
            },
            {
              title: `带看日期`,
              dataIndex: 'dktime',
            },
            {
              title: '带看人',
              dataIndex: 'dkpeople',
            }
           
           
          ];
          const { datanum } =this.state;
        return(
          <div className="daikanzong">
                <Table  columns={columns} dataSource={datanum}    />
               
          </div>
          )
      }
}
  export default daikanzong