import React ,{Component} from 'react';
import{Breadcrumb,Tabs,DatePicker, LocaleProvider,Form,Select,Button} from 'antd'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import {
  G2,
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Legend,
  View,
  Guide,
  Shape,
  Facet,
  Util
} from "bizcharts";
import DataSet from "@antv/data-set";
import axios from 'axios'
import  '../../../style/statistics/telfollow.scss'
import $ from 'jquery'
import { Link } from 'react-router-dom'
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
function onChangeDate(date, dateString) {
  console.log(date, dateString);
}
const { DataView } = DataSet;
const {Option}=Select;
const { Html } = Guide;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class telFollowForm extends Component{
      constructor(props){
        super(props)
        this.state={
          data:[],
          dvv:[],
          colss:[],

          si:[],
          gong:[],
          zong:'',

          cheng:[],
          qu:[],
          zu:[],
          peo:[],



          gids:[],
          sids:[],


        }
      }
      componentDidMount(){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        var num=1;
        if(loginid!='' && user!=""){
          axios.post('/index.php/gen/diangen',{
             name:user,
             num:loginid,
             type:num
          })
          .then((res)=>{
            console.log(res.data.data);
            this.setState({
              data:res.data.data,
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               dvv:dv,
               colss:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }
        this.getCity();
      }
      selectDate=(e)=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        $(e.target).addClass("current").siblings().removeClass("current")
        var num=e.target.getAttribute('data-key');
        localStorage.setItem('timetype',num);
        // console.log(num);
     
      }
      godetail=(e)=>{
        const { gids , sids } =this.state;
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        var key=e.target.getAttribute('data-key');
        var type=localStorage.getItem('timetype');
        console.log(type);
        console.log(key);
        if(key==1){
             this.props.history.push({ pathname:'/statistics/telfollowdetail',data:sids})
        }else if(key==2){
            this.props.history.push({ pathname:'/statistics/telfollowdetail',data:gids})
        }
       
      }
 //区域数据
 getCity(){
  if(loginid!='' && user!=""){
    var loginid=localStorage.getItem('loginid');
    var user=localStorage.getItem('name');
  axios.post('/index.php/qu/one',{ 
    name:user,
    num:loginid
     })
     .then((res)=>{
       if(res.data.code==200){
        console.log(res.data.data);
        var city=res.data.data;
        if(city){
            this.setState({
              cheng:city
            })
        }
       
       }else {
       
       }
     }).catch(error=>{
       console.log(error)
     })
    }
}
 handleSelectCity(value){
  if(loginid!='' && user!=""){
    var loginid=localStorage.getItem('loginid');
    var user=localStorage.getItem('name');
  axios.post('/index.php/qu/two',{ 
    name:user,
    num:loginid,
    id:value
     })
     .then((res)=>{
       if(res.data.code==200){
        console.log(res.data.data);
        var num=res.data.data;
        if(num){
            this.setState({
              qu:num
            })
        }
       
       }else {
       
       }
     }).catch(error=>{
       console.log(error)
     })
    }

}
handleSelectQu(value){
  console.log(value);
  if(loginid!='' && user!=""){
 var loginid=localStorage.getItem('loginid');
 var user=localStorage.getItem('name');
axios.post('/index.php/qu/two',{ 
 name:user,
 num:loginid,
 id:value
  })
  .then((res)=>{
    if(res.data.code==200){
     console.log(res.data.data);
     var data=res.data.data;
     if(data){
         this.setState({
           zu:data
         })
     }
    
    }else {
    
    }
  }).catch(error=>{
    console.log(error)
  })
 }
}
changeZu=(value)=>{
  console.log(value);
  if(loginid!='' && user!=""){
   var loginid=localStorage.getItem('loginid');
   var user=localStorage.getItem('name');
 axios.post('/index.php/qu/peo',{ 
   name:user,
   num:loginid,
   id:value
    })
    .then((res)=>{
      if(res.data.code==200){
       console.log(res.data.data);
       var number=res.data.data;
       if(number){
           this.setState({
             peo:number
           })
       }
      
      }else {
      
      }
    }).catch(error=>{
      console.log(error)
    })
   }
}
//查询数据
handleSubmit = e => {
  e.preventDefault();
  this.props.form.validateFields((err, values) => {
    if (!err) {
      console.log(err);
      console.log('输入的结果为', values);
      var num=localStorage.getItem('timetype');
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
      console.log(num);
         if(loginid!='' && user!=""){
          axios.post('/index.php/gen/diangen',{
             name:user,
             num:loginid,
             type:num,
             value:values
          })
          .then((res)=>{
            console.log(res);
            var    gids=res.data.gids;
            var    sids=res.data.sids;
            console.log(gids,sids);
            this.setState({
                 gids:gids,
                 sids:sids
            })


            console.log(res.data.data);
            var num1=res.data.data;
            console.log(res.data.gids);
            console.log(res.data.sids);
            var  zongdata=0;
            if(num1.length>0){
              num1.map((m,n)=>{
                zongdata+=m.count;
                console.log(zongdata);
              })
            }
          
            this.setState({
              data:res.data.data,
               zong:zongdata
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               dvv:dv,
               colss:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }




    }
  })
}






      render(){
        const { dainum,cheng,qu,zu,peo,total,zong} =this.state;
        const { getFieldDecorator } = this.props.form;
        return(
          <LocaleProvider locale={zhCN}>
          <div className="telfollow">
              <Breadcrumb>
              <Breadcrumb.Item  ><Link to="/statistics/telfollow"> 统计分析</Link></Breadcrumb.Item>
              <Breadcrumb.Item > <Link to="/statistics/telfollow">电话跟进 </Link></Breadcrumb.Item>
          </Breadcrumb>
          <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
                    <Form.Item label="城市" layout="inline">
                      {getFieldDecorator('city', {
                          rules: [{ required: false, message: '请选择省' }],
                        })(
                              <Select  style={{ width:310}} onChange={this.handleSelectCity.bind(this)} allowClear>
                              {
                                cheng.length>0?cheng.map((m,n)=>{
                                      return(
                                        <Option value={m.id} key={m.id}>{m.name}</Option>
                                      )
                                }) : ''
                            }
                          </Select>
                        )}
                      </Form.Item>  
                    <Form.Item label="区域" layout="inline">
                      {getFieldDecorator('area', {
                          rules: [{ required: false, message: '请选择省' }],
                        })(
                          <Select style={{ width:300,marginRight:20}} onSelect={this.handleSelectQu.bind(this)} allowClear>
                          {
                            qu.length>0?qu.map((m,n)=>{
                                  return(
                                  <Option value={m.id} key={n}>{m.name}</Option>
                                  )
                            }) :''
                      } 
                        </Select>
                        )}
                      </Form.Item>   
                    <Form.Item label="组别" layout="inline">
                        {getFieldDecorator('department', {
                            rules: [{ required: false, message: '请选择省' }],
                          })(
                            <Select  style={{ width:300,}} onChange={this.changeZu.bind(this)} allowClear> 
                            {
                                zu.length>0 ? zu.map((m,n)=>{
                                      return(
                                        <Option value={m.id } key={n}>{m.name} </Option>
                                      )
                                }) :''
                            }
                        
              
                          </Select>
                          )}
                        </Form.Item>
                    <Form.Item label="人员" layout="inline">
                        {getFieldDecorator('id', {
                            rules: [{ required: false, message: '请选择省' }],
                          })(
                            <Select
                              placeholder="所有"
                              onChange={this.handleSelectChange}
                              style={{width:300}}
                              allowClear
                            >
                              {
                                  peo.length>0 ? peo.map((m,n)=>{
                                    return(
                                      <Option value={m.id } key={n}>{m.name} </Option>
                                    )
                              }) :''
                              }
                            </Select> 
                          )}
                      </Form.Item>
                  <div className="option">
                      <ul className="time" style={{ cursor:'pointer',fontSize:16}}>
                      <li> </li>
                          <li onClick={this.selectDate.bind(this)} data-key={'1'}>日</li>
                          <li onClick={this.selectDate.bind(this)} data-key={'2'}>周</li>
                          <li onClick={this.selectDate.bind(this)}  data-key={'3'}>月</li>
                          <li onClick={this.selectDate.bind(this)}  data-key={'4'}>年度</li>
                      </ul>
                      {/* <RangePicker onChange={onChangeDate} style={{width:400,marginLeft:80}} /> */}
                </div>
                <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
                        查询
                </Button>

          </Form>


             <br/>
             <br/>
          <p style={{ marginRight:'650px',fontSize:'20px',fontWeight:400,color:'rgba(52,52,52,1)'}}>电话跟进</p>
        <Chart
        height={500}
        width={ 950 }
        data={this.state.dvv}
        scale={this.state.colss}
        padding={[15, 0, 0, -150]}
        forceFit={false}
        style={{ float:'left'}}
      >
    <Coord type={"theta"} radius={0.75} innerRadius={0.7} />
    <Axis name="percent" />
    <Legend
        position="right-top"
        offsetX={-110}
        offsetY={100}
        textStyle={{  fontSize: '18',}}
    />
    <Tooltip
    showTitle={false}
    itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
    />
    <Guide>
    <Html
      position={["50%", "50%"]}
      // html="<div style=&quot;color:#8c8c8c;font-size:1.16em;text-align: center;width:4rem;&quot;>总电话量<br><span style=&quot;color:#262626;font-size:1.5em&quot;>67</span>人</div>"
      html={(xScale, yScale) => {
        console.log(yScale);
        return `<div style="font-size:18px">总电话量${zong}个</div>`; 
      }}
      alignX="middle"
      alignY="middle"
    />
    </Guide>
    <Geom
    type="intervalStack"
    position="percent"
    color="item"
    tooltip={[
      "item*percent",
      (item, percent) => {
        percent =Number(percent*100).toFixed(2) + "%";
        return {
          name: item,
          value: percent
        };
      }
    ]}
    style={{
      lineWidth: 3,
      stroke: "#fff"
    }}
    >
        <Label
              content="item*percent"
              formatter={(item, percent) => {
                var num=percent.point.percent;
                var  a= Number(num*100).toFixed(2);
                var number=num.toFixed(2)*100;
                var ge=percent.point.count;
                // console.log(number,item);
                 return   item + ": " + a +"%-----"+ge+"个"
              }}
              textStyle={{
                fontSize: '18',
              }}
         />
    </Geom>
    </Chart>
     <div className="detail">
       <span onClick={this.godetail.bind(this)} data-key={'1'}>私客量详情</span><br/>
       <span onClick={this.godetail.bind(this)}  data-key={'2'}>公客量详情</span>
     </div>
          </div>
        </LocaleProvider>
          )
      }
}
  const telFollow =Form.create()(telFollowForm);
  export default telFollow;
