import React ,{Component} from 'react';
import { Select,Table } from 'antd';
import axios from 'axios'
class GenJinLiang extends Component{
      constructor(props){
        super(props)
        this.state={
            gennum:[],
        }
      }
     
       componentDidMount(){
              this.genDetail();
       }
       genDetail(){
        const  loginid=localStorage.getItem('loginid');
        const user=localStorage.getItem('name');
        var id=this.props.location.data;
        if(loginid!='' && user!=""){
            axios.post('/index.php/gen/tonggenlist',{
               ids:id,
               num:loginid,
               name:user
             })
             .then((res)=>{
               //  console.log(res);
               if(parseInt(res.data.code) === 200){
                 //  console.log(res);
                  var gen=res.data.data
                  if(gen.length>0){
     
                 
                      console.log(gen);
                      var gendata=[]
                      gen.map((v,j)=>{
                        gendata.push({
                            key:v.id,
                            kename:v.name,
                            project:v.pro,
                            gentime: v.time,
                            gjcontent: v.content,
                            yperson:v.s_id,
                      })
                      this.setState({
                       gennum:gendata,
                      })
                  })
                 }
                 else{
                   this.setState({
                     gennum:'',
                    })
                 }
               }else{
                  console.log("请求数据失败")
               }
             }).catch((error)=>{
               console.log(error)
             })
           }
     
          }
     
      render(){
        const columns1 = [
            {
                title: '客户姓名',
              dataIndex: 'kename',
            },
            {
              title: '进客项目',
              dataIndex: 'project',
             
            },
            {
                title: '跟进时间',
                dataIndex: 'gentime',
              },
            {
              title: '跟进内容',
              dataIndex: 'gjcontent',
            },
            {
              title: '跟进人',
              dataIndex: 'yperson',
            },
          ];
        return(
          <div className="genjinliang">
                 <Table  columns={columns1} dataSource={this.state.gennum} size="middle" className="genjintablle" />
          </div>
          )
      }
}
  export default GenJinLiang