import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Cascader,
  Tabs,
 Table,
 Icon,
 LocaleProvider,
 Menu, 
 Dropdown, 
 DatePicker } from 'antd';
 import {Link} from 'react-router-dom'
 import zhCN from 'antd/lib/locale-provider/zh_CN';
 import axios from 'axios' 
 import '../../style/customersuccess.scss'
 function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
function onChangeTime(date, dateString) {
  console.log(date, dateString);
}
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
const {Option}=Select
const { TabPane } = Tabs;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class customerSuccessForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
          obj:[],
          chengData:[],
          cheng:[],
          qu:[],
          zu:[],
          peo:[]
        }
      }
      componentDidMount(){
        this.region();
        //项目列表
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/recording/recordUser',{
          name:user,
          num:loginid
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
             console.log(res);
             var number=res.data.data;
             if(number.length>0){
             number.map((v,j)=>{
             this.state.datanum.push({
                     key:v.id,
                    //  tag:`${v.grade}/${v.label}`,
                     tag:v.grade,
                     label:v.label,
                     re:v.re,
                     dai:v.dai,
                     fu:v.fu,



                     customername:v.name,
                     dkproject:v.project,
                     jkorigin:v.port,
                     dktime:v.r_time,
                     dkpeople:v.s_id,
                 })
             var  datanum=this.state.datanum
                 this.setState({
                  datanum:datanum,
                  loading:false
                 })
             })
            }else{
              this.setState({
                datanum:'',
                loading:false
               })
            }
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
     //区域数据
      }
     
      this.getCity();

      }

      getCity(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/qu/one',{ 
          name:user,
          num:loginid
           })
           .then((res)=>{
             if(res.data.code==200){
              console.log(res.data.data);
              var city=res.data.data;
              if(city){
                  this.setState({
                    cheng:city
                  })
              }
             
             }else {
             
             }
           }).catch(error=>{
             console.log(error)
           })
          }
      }
      handleSelectCity(value){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/qu/two',{ 
          name:user,
          num:loginid,
          id:value
           })
           .then((res)=>{
             if(res.data.code==200){
              console.log(res.data.data);
              var num=res.data.data;
              if(num){
                  this.setState({
                    qu:num
                  })
              }
             
             }else {
             
             }
           }).catch(error=>{
             console.log(error)
           })
          }
    
      }
      handleSelectQu(value){
        console.log(value);
        if(loginid!='' && user!=""){
       var loginid=localStorage.getItem('loginid');
       var user=localStorage.getItem('name');
     axios.post('/index.php/qu/two',{ 
       name:user,
       num:loginid,
       id:value
        })
        .then((res)=>{
          if(res.data.code==200){
           console.log(res.data.data);
           var data=res.data.data;
           if(data){
               this.setState({
                 zu:data
               })
           }
          
          }else {
          
          }
        }).catch(error=>{
          console.log(error)
        })
       }
    }
    changeZu=(value)=>{
      console.log(value);
      if(loginid!='' && user!=""){
       var loginid=localStorage.getItem('loginid');
       var user=localStorage.getItem('name');
     axios.post('/index.php/qu/peo',{ 
       name:user,
       num:loginid,
       id:value
        })
        .then((res)=>{
          if(res.data.code==200){
           console.log(res.data.data);
           var number=res.data.data;
           if(number){
               this.setState({
                 peo:number
               })
           }
          
          }else {
          
          }
        }).catch(error=>{
          console.log(error)
        })
       }
 }
        //表单提交
        handleSubmit = e => {
          e.preventDefault();
          this.props.form.validateFields((err, values) => {
            if (!err) {
              console.log(err);
              console.log('输入的结果为', values);
              if(loginid!='' && user!=""){
                var loginid=localStorage.getItem('loginid');
                var user=localStorage.getItem('name');
              axios.post('/index.php/recording/recordUserSou',{
                value:values,
                name:user,
                num:loginid
              })
              .then((res)=>{
                if(parseInt(res.status) === 200){
                  console.log(res.data.data);
                  var number=res.data.data;

                  if(number.length>0){
                  var arr=[];
                  number.map((v,j)=>{
                 arr.push({
                          key:v.id,
                          tag:v.grade,
                          label:v.label,
                          re:v.re,
                          dai:v.dai,
                          fu:v.fu,

                          customername:v.name,
                          dkproject:v.project,
                          jkorigin:v.port,
                          dktime:v.r_time,
                          dkpeople:v.s_id,
                      })
                      this.setState({
                       datanum:arr,
                       loading:false
                      })
                    })
                  }else{
                    this.setState({
                      datanum:'',
                      loading:false
                     })
                  }
                }else{
                   console.log("请求数据失败")
                }
              }).catch((error)=>{
                console.log(error)
              })
            }

            }else{
  
            }
  
  
          });
        }
        region(){
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
          axios.post('/index.php/pro/getarea',{
            name:user,
            num:loginid
          })
          .then((res)=>{
            var arr=res.data.data
            const list = Object.keys(arr).map((item, index) => ({ value:arr[item],label: item}));
            list.map(ite => {
              const shi = Object.keys(ite.value).map((j, index) => ({ value:ite.value[j],label: j}));
               this.state.obj.push({
                'value':ite.label,
                'label':ite.label,
                'children': shi.map(item=>{
                  const qu= Object.keys(item.value).map((k, index) => ({ value:item.value[k],label: item.value[k]}));
                  const  xian={
                    'value':item.label,
                    'label':item.label,
                    'children':qu
                  };
                  return xian;
                   })
                  
              })
             
            })
            console.log(this.state.obj);
          }).catch((error)=>{
          console.log(error)
          })
        }
        }
       
        getChengjiaoData(){
          var id= sessionStorage.getItem('cjid');
          console.log(id);
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
           axios.post('/index.php/recording/uindex',{
                id:id,
                name:user,
                num:loginid
           })
           .then((res)=>{
             if(parseInt(res.status) === 200){
              
                var number=res.data.data
               console.log(number);
                  var array=[];
                number.map((v,j)=>{
                 array.push({
                 key: v.id,
                 cjtime: v.time,
                cjproject:v.project,
                yepeople:v.s_id,
                rtype:v.from
                    })
              this.setState({
                chengData:array
              })
                 })
             }else{
                console.log("请求数据失败")
             }
           }).catch((error)=>{
             console.log(error)
           })
          }
        }
        tiaoDetail(num){
          this.props.history.push({pathname:'/customermanage/customerdetail',data:num});
  
        }
      render(){
        const { dainum,cheng,qu,zu,peo,chengData} =this.state;
        const paginationProps = {
          showSizeChanger: true,
          showQuickJumper: {goButton:<Button type="primary">确定</Button>},
          showTotal: ((total,count) => {
            console.log(count);
             return `共 ${total} 条`;
          }),
          // showTotal: () => `共${totals}条`,
          // pageSize: this.state.pageSize,
          // current: page.pageNum,
          // total: page.total,
          // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
          // onChange: (current) => this.changePage(current),
        };
        const columns = [
          {
            title: '',
            dataIndex: 'tag',
            width:30,
              render:(text,record) =>{
              var lei=record.tag
              //  var type=record.label;
              //  var re=record.re;
              //  var dai=record.dai;
                // console.log(record);
               var str=""
               if( lei =="A" || lei=="A类" ){
                  return(
                     <a className="iconfont icon-zhongdianbiaozhu " href="javascript:;" style={{color:'red'}}></a> 
                  )
               }
            }

          },
          {
            title: '标',
            dataIndex: 'tag1',
            width:30,
            render:(text,record) =>{
              var type=record.label;
              if(type=="一客多看"){
                return(
                  <a className="iconfont icon-ai-connection" href="javascript:;" style={{color:'rgba(101,195,255,1)'}}></a> 
                )
             }
            }
          },
          {
            title: '',
            dataIndex: 'tag2',
            width:30,
            render:(text,record) =>{
              var num=record.fu;
              if(num=="复看"){
                return(
                  <a className="iconfont icon-renshu" href="javascript:;" style={{color:'rgba(196,132,255,1)'}}></a> 
                )
             }
            }
          },
          {
            title: '签',
            dataIndex: 'tag3',
            width:30,
              render:(text,record) =>{
              var dai=record.dai;
              if(dai=="新客" || dai=="老客" ){
                return(
                  <a className="iconfont icon-wo" href="javascript:;" style={{color:'rgba(41,204,114,1)'}}></a> 
                )
             }
            }
          },
          {
            title: '',
            dataIndex: 'tag4',
            width:30,
            render:(text,record) =>{
              var re=record.re;
            if(re=="成交"){
              return(
                <a className="iconfont icon-chengjiao" href="javascript:;" style={{color:'rgba(255,191,52,1)'}}></a> 
              )
            }
          }
          },




          {
            title: '客户姓名',
            dataIndex: 'customername',
          },
          {
            title: '成交项目',
            dataIndex: 'dkproject',
          },
          {
            title: '进客来源',
            dataIndex: 'jkorigin',
          },
          {
            title: '成交日期',
            dataIndex: 'dktime',
          },
          {
            title: '带看人',
            dataIndex: 'dkpeople',
          },
          {
            title: '操作',
            dataIndex: 'handle',
            render:(text ,record) =><div> <span style={{color:"#1890FF",cursor:"pointer"}} onClick={this.tiaoDetail.bind(this,record.key)}>详情</span></div>,
          },

        ];
        const { getFieldDecorator } = this.props.form;
        const columns1 = [
          {
            title: '成交时间',
            dataIndex: 'cjtime',
           
          },
          {
            title: '成交项目',
            dataIndex: 'cjproject',
          },
       
          {
            title: '业务员',
            dataIndex: 'yepeople',
          },
          {
            title: '录入方式',
            dataIndex: 'rtype',
          },
        ];
        
       const data2=[];
       for (let i = 0; i < 5; i++) {
        data2.push({
          key: i,
          cjtime: '09-04-04 08：12',
          cjproject: "众安嘉润公馆",
          yepeople:'林北',
          rtype:'电脑端'
        });
      }
      const rowSelection = {
        type:'radio',
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
          var key=selectedRowKeys[0];
          console.log(key);
          sessionStorage.setItem('cjid',key);
          this.getChengjiaoData();
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
        return(
          <LocaleProvider locale={zhCN}>
          <div className="customersuccess">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item  ><Link  to="/customermanage/customerlist"> 客户管理</Link></Breadcrumb.Item>
                  <Breadcrumb.Item ><Link to="/customermanage/customersuccess">成交客户 </Link></Breadcrumb.Item>
                </Breadcrumb>
             </div>
               <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
               <Form.Item label="类别">
              {getFieldDecorator('type', {
                rules: [{ required: false, message: '请选择类型!' }],
              })(
                <Select
                style={{ width:'310px'}}
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                  allowClear
                >
                  <Option value="公寓">公寓</Option>
                  <Option value="住宅">住宅</Option>
                  <Option value="商铺">商铺</Option>
                  <Option value="写字楼">写字楼</Option>
                </Select>,
              )}
            </Form.Item>  

           

            {/* <Form.Item label="区域" layout="inline">
            {getFieldDecorator('address1', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Cascader options={this.state.obj} onChange={onChange} placeholder="请选择"  style={{ width:'300px'}} />
              )}
            </Form.Item> */}
            
           
              <Form.Item label="项目" layout="inline">
            {getFieldDecorator('project', {
                rules: [{ required: false, message: '请输入项目名称' }],
              })(
                <Input onChange={onChangeTime} style={{width:"310px",}} />
              )}
              </Form.Item>
               {/** 电话*/}
               <Form.Item label="电话" >
              {getFieldDecorator('tel', {
                rules: [{ required: false, message: '请输入电话' }],
              })(
                <Input 
                  placeholder="电话"
                  style={{width:"300px",}}
                />
              )}
              </Form.Item>
               {/** 进客来源*/}
               <Form.Item label="进客来源" >
                {getFieldDecorator('port', {
                    rules: [{ required: false, message: '请选择省' }],
                  })(
                    <Select
                    style={{ width:'300px'}}
                      placeholder="请选择"
                      onChange={this.handleSelectChange}
                      allowClear
                    >
                      <Option value="线上推广1">线上推广1</Option>
                      <Option value="线上推广2">线上推广2</Option>
                      <Option value="线上电话1">线上电话1</Option>
                      <Option value="线上电话2">线上电话2</Option>
                      <Option value="自主">自主</Option>
                      <Option value="咨询">咨询</Option>
                      <Option value="新媒体">新媒体</Option>
                      <Option value="百度文库">百度文库</Option>
                    </Select>,
                  )}
              </Form.Item>
              {/** 城市*/}
         <Form.Item label="城市" layout="inline">
            {getFieldDecorator('city', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                    <Select  style={{ width:310}} onChange={this.handleSelectCity.bind(this)}   allowClear>
                    {
                      cheng.length>0?cheng.map((m,n)=>{
                            return(
                              <Option value={m.id} key={m.id}>{m.name}</Option>
                            )
                      }) : ''
                    }
                </Select>
              )}
            </Form.Item>
             {/** 项目区域*/}
            <Form.Item label="区域" layout="inline">
            {getFieldDecorator('area', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select style={{ width:300,marginRight:20}} onSelect={this.handleSelectQu.bind(this)}   allowClear>
                {
                  qu.length>0?qu.map((m,n)=>{
                        return(
                         <Option value={m.id} key={n}>{m.name}</Option>
                        )
                  }) :''
             } 
               </Select>
              )}
            </Form.Item>
             {/** 组别*/}
            <Form.Item label="组别" layout="inline">
            {getFieldDecorator('department', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select  style={{ width:300,}} onChange={this.changeZu.bind(this)}   allowClear>
                {
                     zu.length>0 ? zu.map((m,n)=>{
                          return(
                            <Option value={m.id } key={n}>{m.name} </Option>
                          )
                     }) :''
                }
             
  
              </Select>
              )}
            </Form.Item>
               {/** 人员*/}
            <Form.Item label="人员" >
                {getFieldDecorator('id', {
                    rules: [{ required: false, message: '请选择省' }],
                  })(
                    <Select
                      placeholder="所有"
                      onChange={this.handleSelectChange}
                      style={{width:300}}
                      allowClear
                    >
                      {
                          peo.length>0 ? peo.map((m,n)=>{
                            return(
                              <Option value={m.id } key={n}>{m.name} </Option>
                            )
                       }) :''
                      }
                    </Select> 
                  )}
              </Form.Item>
              <Form.Item label="时间" layout="inline">
                {getFieldDecorator('time', {
                    rules: [{ required: false, message: '请选择省' }],
                  })(
                    <RangePicker onChange={onChangeTime} style={{width:"310px",marginRight:"20px"}}   allowClear/>
                  )}
              </Form.Item>
              
              
              <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
              查询
            </Button>
             </Form>
            <div className="project-table">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading} />
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p> */}
            </div>

            <div className="project-tab">
            <Tabs defaultActiveKey="1" onChange={callback}>
            <TabPane tab="成交记录" key="1">
                    <Table  columns={columns1} dataSource={chengData} size="middle" className="genjintablle" />
            </TabPane>
           
          </Tabs>
            </div>

            

          </div>
          </LocaleProvider>
          )
      }
}
const customerSuccess = Form.create()(customerSuccessForm);
export default customerSuccess