import React ,{Component} from 'react';
import{Breadcrumb,Tabs,DatePicker,LocaleProvider ,Form,Select,Button} from 'antd'
import {
  G2,
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Legend,
  View,
  Guide,
  Shape,
  Facet,
  Util
} from "bizcharts";
import DataSet from "@antv/data-set";
import axios from 'axios'
import '../../style/statistics/cusrate.scss'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import {Link} from 'react-router-dom'
import  $ from 'jquery'
function onChangeDate(date, dateString) {
  console.log(date, dateString);
}
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
const { DataView } = DataSet;
const { Html } = Guide;
const {Option}= Select;
class cusRateForm extends Component{
      constructor(props){
        super(props)
        this.state={
          data:[],
          dvv:[],
          colss:[],
          zong:[],


          cheng:[],
          qu:[],
          zu:[],
          peo:[],
        }
      }
      componentDidMount(){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        var num=1;
        if(loginid!='' && user!=""){
          axios.post('/index.php/user/bing',{
             name:user,
             num:loginid,
             type:num
          })
          .then((res)=>{
            console.log(res.data.n);
            var num1=res.data.n
            var  zongdata=0;
            num1.map((m,n)=>{
                  zongdata+=m.count;
                  console.log(zongdata);
            })
            this.setState({
              data:res.data.n,
               zong:zongdata
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               dvv:dv,
               colss:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }
          this.getCity();
      }
      selectDate=(e)=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        $(e.target).addClass("current").siblings().removeClass("current")
        var num=e.target.getAttribute('data-key');
        console.log(num);
        localStorage.setItem('liyong',num);
        
      }

//区域数据
getCity(){
  if(loginid!='' && user!=""){
    var loginid=localStorage.getItem('loginid');
    var user=localStorage.getItem('name');
  axios.post('/index.php/qu/one',{ 
    name:user,
    num:loginid
     })
     .then((res)=>{
       if(res.data.code==200){
        console.log(res.data.data);
        var city=res.data.data;
        if(city){
            this.setState({
              cheng:city
            })
        }
       
       }else {
       
       }
     }).catch(error=>{
       console.log(error)
     })
    }
}
 handleSelectCity(value){
  if(loginid!='' && user!=""){
    var loginid=localStorage.getItem('loginid');
    var user=localStorage.getItem('name');
  axios.post('/index.php/qu/two',{ 
    name:user,
    num:loginid,
    id:value
     })
     .then((res)=>{
       if(res.data.code==200){
        console.log(res.data.data);
        var num=res.data.data;
        if(num){
            this.setState({
              qu:num
            })
        }
       
       }else {
       
       }
     }).catch(error=>{
       console.log(error)
     })
    }

}
handleSelectQu(value){
  console.log(value);
  if(loginid!='' && user!=""){
 var loginid=localStorage.getItem('loginid');
 var user=localStorage.getItem('name');
axios.post('/index.php/qu/two',{ 
 name:user,
 num:loginid,
 id:value
  })
  .then((res)=>{
    if(res.data.code==200){
     console.log(res.data.data);
     var data=res.data.data;
     if(data){
         this.setState({
           zu:data
         })
     }
    
    }else {
    
    }
  }).catch(error=>{
    console.log(error)
  })
 }
}
changeZu=(value)=>{
console.log(value);
if(loginid!='' && user!=""){
 var loginid=localStorage.getItem('loginid');
 var user=localStorage.getItem('name');
axios.post('/index.php/qu/peo',{ 
 name:user,
 num:loginid,
 id:value
  })
  .then((res)=>{
    if(res.data.code==200){
     console.log(res.data.data);
     var number=res.data.data;
     if(number){
         this.setState({
           peo:number
         })
     }
    
    }else {
    
    }
  }).catch(error=>{
    console.log(error)
  })
 }
}


//查询搜索
handleSubmit = e => {
  e.preventDefault();
  this.props.form.validateFields((err, values) => {
    if (!err) {
      console.log(err);
      console.log('输入的结果为', values);
      var numid=localStorage.getItem('liyong');
      console.log(numid);
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
      if(loginid!='' && user!=""){
          axios.post('/index.php/user/bing',{
             name:user,
             num:loginid,
             type:numid,
             value:values
          })
          .then((res)=>{
            console.log(res.data.n);
            var num1=res.data.n
            var  zongdata=0;
            num1.map((m,n)=>{
                  zongdata+=m.count;
                  console.log(zongdata);
            })
            this.setState({
              data:res.data.n,
              zong:zongdata
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               dvv:dv,
               colss:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }

    }
  
})

}






      render(){
        const { dainum,cheng,qu,zu,peo,total,zong} =this.state;
        const { getFieldDecorator } = this.props.form;
        return(
          <LocaleProvider locale={zhCN}>
          <div className="cusrate">
          <Breadcrumb>
              <Breadcrumb.Item ><Link to="/statistics/cusrate"> 统计分析</Link></Breadcrumb.Item>
              <Breadcrumb.Item><Link to="/statistics/cusrate"> 客户利用率</Link> </Breadcrumb.Item>
          </Breadcrumb>
          <Form layout="inline"  onSubmit={this.handleSubmit}>
                <Form.Item label="城市" layout="inline">
                      {getFieldDecorator('city', {
                          rules: [{ required: false, message: '请选择省' }],
                        })(
                              <Select  style={{ width:310}} onChange={this.handleSelectCity.bind(this)}  allowClear>
                              {
                                cheng.length>0?cheng.map((m,n)=>{
                                      return(
                                        <Option value={m.id} key={m.id}>{m.name}</Option>
                                      )
                                }) : ''
                            }
                          </Select>
                        )}
                </Form.Item>  
                <Form.Item label="区域" layout="inline">
                      {getFieldDecorator('area', {
                          rules: [{ required: false, message: '请选择省' }],
                        })(
                          <Select style={{ width:300,marginRight:20}} onSelect={this.handleSelectQu.bind(this)}  allowClear>
                          {
                            qu.length>0?qu.map((m,n)=>{
                                  return(
                                  <Option value={m.id} key={n}>{m.name}</Option>
                                  )
                            }) :''
                      } 
                        </Select>
                        )}
                  </Form.Item>  
                  <Form.Item label="组别" layout="inline">
                        {getFieldDecorator('department', {
                            rules: [{ required: false, message: '请选择省' }],
                          })(
                            <Select  style={{ width:300,}} onChange={this.changeZu.bind(this)}  allowClear>
                            {
                                zu.length>0 ? zu.map((m,n)=>{
                                      return(
                                        <Option value={m.id } key={n}>{m.name} </Option>
                                      )
                                }) :''
                            }
                        
              
                          </Select>
                          )}
                    </Form.Item>
                    <Form.Item label="人 员" >
                        {getFieldDecorator('id', {
                            rules: [{ required: false, message: '请选择省' }],
                          })(
                            <Select
                              placeholder="所有"
                              onChange={this.handleSelectChange}
                              style={{width:310,marginLeft:25}}
                              allowClear
                            >
                              {
                                  peo.length>0 ? peo.map((m,n)=>{
                                    return(
                                      <Option value={m.id } key={n}>{m.name} </Option>
                                    )
                              }) :''
                              }
                            </Select> 
                          )}
                   </Form.Item>
                   <div className="option">
                    <ul className="time"> 
                    <li> </li>
                        <li onClick={this.selectDate.bind(this)} data-key={'1'}>周</li>
                        <li onClick={this.selectDate.bind(this)}  data-key={'2'}>月</li>
                        <li onClick={this.selectDate.bind(this)} data-key={'3'}>季度</li>
                        <li onClick={this.selectDate.bind(this)}  data-key={'4'}>年度</li>
                    </ul>
                {/* <RangePicker  onChange={onChangeDate} style={{width:400,marginRight:80}} /> */}
                </div>
                <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
                        查询
                </Button>
          </Form>
           <p  className="title" style={{ marginRight:'660px',fontWeight:400 ,fontSize:18   ,color:'rgba(51,51,51,1)'}}>客户利用率</p>

        <Chart
    height={500}
    width={ 1025 }
    data={this.state.dvv}
    scale={this.state.colss}
    padding={[15, 0, 0, -200]}
    forceFit={false}
    style={{ float:'left'}}
      >
    <Coord type={"theta"} radius={0.75} innerRadius={0.7} />
    <Axis name="percent" />
    <Legend
    position="right-top"
    offsetX={-130}
    offsetY={100}
    textStyle={{  fontSize: '18',}}
    />
    <Tooltip
    showTitle={false}
    itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
    />
    <Guide>
    <Html
      position={["50%", "50%"]}
      html={(xScale, yScale) => {
        console.log(yScale);
        return `<div style="font-size:18px">总进客次${zong}个</div>`; 
      }}
      alignX="middle"
      alignY="middle"
    />
    </Guide>
    <Geom
    type="intervalStack"
    position="percent"
    color="item"
    tooltip={[
      "item*percent",
      (item, percent) => {
        percent =Number(percent*100).toFixed(2) + "%";
        return {
          name: item,
          value: percent
        };
      }
    ]}
    style={{
      lineWidth: 3,
      stroke: "#fff"
    }}
    >
      <Label
              content="item*percent"
              formatter={(item, percent) => {
                var num=percent.point.percent;
                var  a= Number(num*100).toFixed(2);
                var number=num.toFixed(2)*100;
                var ge=percent.point.count;
                // console.log(number,item,num);
                 return   item + ": " + a +"%-----"+ge+"次"
              }}
              textStyle={{
                fontSize: '18',
              }}
         />
    </Geom>
    </Chart>
        <Link to="/customermanage/sawcustomer"  style={{ marginLeft:1000,color:'rgba(24,144,255,1)',fontSize:'18px',display:'block',marginTop:101}}>查看详情</Link>
          </div>
          </LocaleProvider>
          )
      }
}

  const  cusRate =Form.create()(cusRateForm);
  export default cusRate